import React from 'react';
import { graphql } from 'gatsby';

import Image from 'gatsby-image';
import Pills from '../components/pills';
import Layout from '../components/layout';
import SEO from '../components/seo';
import { rhythm, scale } from '../utils/typography';

interface TemplateProps {
  data: any;
  pageContext: any;
  location: Location;
}

const Template: React.FunctionComponent<TemplateProps> = ({ data, location }) => {
  const { markdownRemark: paperReview, site } = data;
  const { title: siteTitle } = site.siteMetadata;
  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title={`${paperReview.frontmatter.title}`}
        description={paperReview.frontmatter.description || paperReview.excerpt}
        keywords={paperReview.frontmatter.keywords}
      />
      <div className="paperReview">
        <h1 style={{ marginTop: 0 }}>
          {paperReview.frontmatter.published ? '' : 'DRAFT: '}
          {paperReview.frontmatter.summaryType}&mdash;{paperReview.frontmatter.title}
        </h1>
        <small
          style={{
            ...scale(-1 / 5),
            display: `block`,
            marginTop: rhythm(-1),
          }}
          title={paperReview.frontmatter.shortDate}
        >
          {paperReview.frontmatter.shortDate}
        </small>
        <Pills items={paperReview.frontmatter.categories} />
        <Image
          fluid={paperReview.frontmatter.cover.childImageSharp.fluid}
          alt={paperReview.frontmatter.title}
          className="u-full-width"
          style={{
            marginTop: rhythm(1),
          }}
        />
        <hr
          style={{
            margin: `${rhythm(1)} 0`,
          }}
        />
        <div style={{ marginTop: rhythm(1) }}>
          <strong>ðŸ“– Link to the Paper - </strong>
          <a rel="noreferrer" target="_blank" href={paperReview.frontmatter.paperPDFLink}>
            {paperReview.frontmatter.title}
          </a>
          <div>
            <small>{paperReview.frontmatter.reference}</small>
          </div>
          {paperReview.frontmatter.projectLink ? (
            <div>
              <strong> ðŸ’¡ Link to Project Webstie - </strong>
              <a rel="noreferrer" target="_blank" href={paperReview.frontmatter.projectLink}>
                {paperReview.frontmatter.projectTitle || paperReview.frontmatter.title}
              </a>
            </div>
          ) : (
            ''
          )}
        </div>
        <hr
          style={{
            margin: `${rhythm(1)} 0`,
          }}
        />
        <div style={{ marginTop: rhythm(1) }} dangerouslySetInnerHTML={{ __html: paperReview.html }} />
        <hr />
      </div>
    </Layout>
  );
};

export default Template;

export const pageQuery = graphql`
  query TalkBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      fields {
        slug
      }
      frontmatter {
        shortDate: date(formatString: "MMMM DD, YYYY")
        title
        categories
        summaryType
        paperPDFLink
        projectLink
        projectTitle
        reference
        published
        cover {
          childImageSharp {
            fluid(maxWidth: 1200) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;
